<template>
    <div class="login_warper">
        <div>
            <label for="rootCompanyId">公司编码</label>
            <input type="text" v-model="rootCompanyId" name="" id="rootCompanyId" />
        </div>
        <div>
            <label for="rootCompanyId">用户名</label>
            <input type="text" v-model="loginName" name="" id="loginName" />
        </div>
        <div>
            <label for="rootCompanyId">密码</label>
            <input type="password" v-model="password" name="" id="password" />
            <input type="hidden" name="rememberMe" v-model="rememberMe" />
        </div>
        <div>

            <span class="btnlogin" @click.stop="login">面试官登录</span>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { api, dicCode } from '../../static/dicConfig'
export default {
    data() {
        return {
            // rootCompanyId: null,
            // loginName: null,
            // password: null,
            // rememberMe: false
            rootCompanyId: '10890',
            loginName: 'admin',
            password: 'admin111111',
            rememberMe: false
        }
    },
    methods: {
        login() {
            console.log(api.host)
            let self = this;
            // debugger
            let postData=new FormData();
            postData.append("rootCompanyId",rootCompanyId);
            postData.append("loginName",loginName);
            postData.append("password",password);
            postData.append("rememberMe",false);
            axios.post(api.host + "user/login", postData)
                .then((result) => {
                    if (result.status === 200) {
                        console.log(result);
                        if (result.data) {
                            if (result.data.returncode) {
                                self.$router.push({ name: "ExaminationRoom" });
                            }
                        }
                    }
                }).catch((r)=>{console.log(r);})

        }
    }
}
</script>

 <style lang="less" scoped>
@import "../assets/css/login.less";
</style>



